.viewUser {
  display: flex;
  /* justify-content: center; */
  width: 100%;
  flex-wrap: wrap;
}
.user {
  width: 300px;
  margin: 1rem;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 5px 10px 10px grey;
  background-color: rgb(194, 235, 237);
  transition: 0.3s;
}
.user:hover {
  transform: scale(1.01);
  transition: 0.3s;
}
.user .btns {
  width: 100%;
  text-align: right;
}
.user .btns button {
  margin: 0 10px;
  padding: 6px 15px;
  border-radius: 4px;
  border: none;
  color: #fff;
  cursor: pointer;
}
.user .btns .green {
  background-color: rgb(141, 183, 56);
  margin: 0 10px;
  padding: 6px 15px;
  border-radius: 4px;
  border: none;
  color: #fff;
  cursor: pointer;
}


.user .btns .red {
  background-color: rgb(216, 73, 73);
}
.user .btns .red:hover {
  background-color: rgb(242, 10, 10);
}
.user .btns .green:hover {
  background-color: rgb(89, 132, 5);
}
.addLink {
  margin: 1rem;
  padding: 9px 2rem;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  background-color: blueviolet;
  transition: 0.2s;
}
.addLink:hover{
    transform: scale(1.02);
  transition: 0.2s;

}
.addBtn{
    width: 100%;
    text-align: center;
}
.addLink a{
  color: #fff;
text-decoration: none;
}