.top-level {
  background-image: linear-gradient(rgb(59, 135, 241), rgb(243, 147, 243));
}
.formContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  height: 100vh;
  /* align-items: center; */
}

form {
  width: 350px;
  border: 1px solid grey;
  padding: 1rem;
  text-align: center;
  border-radius: 10px;
  height: 350px;
  margin-top: 1rem;
  background-color: rgb(50, 6, 88);
}
form input {
  margin: 10px 5px;
  padding: 8px 5px;
}
form .inputBox {
  width: 300px;
  border-radius: 5px;
  transition: 0.4s;
}
form .inputBox:hover {
  transform: scale(1.05);
  transition: 0.4s;
}
form label {
  color: #fff;
}
.addUserContainer {
  width: 100%;
  text-align: center;
  padding: 1rem 0;
}
.addUserContainer h1 {
  color: #fff;
  font-weight: 500;
  font-size: 30px;
}
form .addbtn {
  padding: 9px 30px;
  margin: 1rem 0;
  background-color: cornflowerblue;
  color: #fff;
  border: none;
  border-radius: 4px;
}

 
